<template>
	<view class="organization">
		<view class="uni-flex uni-column">
			<view class="login flex-item">机构信息</view>
			<view class="flex-item">
				<form @submit="formSubmit">
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="机构名称" />
					</view>
					<view class="uni-form-item uni-column">
						<view class="uni-list">
							<view class="uni-list-cell">
								<view class="uni-list-cell-db">
									<picker @change="bindPickerChange" :value="index" :range="array">
										<view class="uni-input">{{array[index]}}</view>
									</picker>
								</view>
							</view>
						</view>
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="负责人姓名" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="负责人电话" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="机构地址" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="详细地址" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="机构电话" />
					</view>
					<view class="uni-form-item uni-column">
						<view class="uni-textarea">
							<textarea placeholder="机构简介" class="uni-input" @blur="bindTextAreaBlur" auto-height />
						</view>
					</view>
					<view class="uni-btn-v">
						<button class="submit" form-type="submit">提交</button>
					</view>
				</form>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'picker',
				array: ['请选择机构品类', '少儿培训', '初高中辅导', '外语培训', '考研培训', 'IT/互联网', '设计培训', '驾校报考', '职业资格', '兴趣爱好'],
				index: 0,
			}
		},
		methods: {
			formSubmit: function(e) {
				console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
				uni.request({
					url: 'http://www.aolige.cn/apis/js/a/login', //仅为示例，并非真实接口地址。
					method: 'POST',
					data: {
						username: this.phone,
						password: this.password,
					},
					header: {
						'custom-header': 'hello' //自定义请求头信息
					},
					success: (res) => {
						console.log(res.data);
					}
				});
			},
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value
			},
			 bindTextAreaBlur: function (e) {
			            console.log(e.detail.value)
			}
		},

	}
</script>

<style>
	.organization {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 20px;
		box-sizing: border-box;
	}

	.login {
		width: 155rpx;
		padding: 0 0 5rpx 0;
		border-bottom: 3px solid #007AFF;
		margin-bottom: 20px;
	}

	.uni-input {
		width: 100%;
		border-bottom: 1px solid #DCDEE0;
		padding: 20rpx 0;
		margin: 20rpx 0;
	}

	.submit {
		margin-top: 80rpx;
		background-color: #007AFF;
		color: #FFFFFF;
	}
</style>
